#root{
    .join-page{
        height: 100%;
        display: flex;
        justify-content: center;
        .join-wrapper{
            width: 350px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 10px;
            .join-box{
                border-radius: 1px;
                border: 1px solid rgb(219,219,219);
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 46px 40px 38px 40px;
                text-align: center;
                &>.logo{
                    background-image: url(/public/assets/main/instagam.png);
                    background-position: 0px -128px;
                    background-size: 439px 408px;
                    width: 175px;
                    height: 51px;

                }
                &>.title{
                    font-size: 16px;
                    font-weight: 700;
                    color: rgb(115,115,115);
                    margin-top: 8px;
                }
                .facebook-login{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 8px;
                    margin: 18px 0;
                    border: unset;
                    color: white;
                    font-weight: 800;
                    font-size: 14px;
                    cursor: pointer;
                    background-color: rgb(0,149,246);
                    border-radius: 8px;
                    width: 268px;
                    height: 34px;
                    padding: 7px 16px;
                    &>.facebook-logo{
                        background-image: url(/public/assets/main/instagam.png);
                        width: 16px;
                        height: 16px;
                        background-position: -364px -353px;
                        background-size: 440px 441px;
                    }
                }
                .or-wrap{
                    display: flex;
                    align-items: center;
                    width: 268px;
                    margin-bottom: 18px;
                    &>.line{
                        border: 0.5px solid rgb(219,219,219);
                        height: 0;
                        width: 100%;
                    }
                    .or{
                        margin: 0 18px;
                        color: rgb(115,115,115);
                        font-size: .8125rem;
                        font-weight: 600;
                        width: 60px;
                    }
                }
                &>.text{
                margin: 10px 0 18px 0;
                    line-height: 16px;
                    font-weight: 400;
                    color: rgb(115,115,115);
                    font-size: 12px;
                    .read-more{
                        border: unset;
                        background: unset;
                        color: rgb(0,55,107);
                        cursor: pointer;
                        font-weight: 400;
                        font-size: 12px;
                    }
                }
                .disabled-btn{
                    border: unset;
                    color: white;
                    background-color: rgb(0,149,246);
                    opacity: 0.7;
                    border-radius: 8px;
                    font-size: 14px;
                    font-weight: 600;
                    width: 268px;
                    height: 32px;
                }
                &>.join-btn{
                    opacity: 1;
                    cursor: pointer;
                }
            }
            .login-box{
                border-radius: 1px;
                border: 1px solid rgb(219,219,219);
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 25px 0;
                &>.login-wrap{
                    font-size: 14px;
                    font-weight: 450;
                    &>.login-btn{
                        border: unset;
                        background: unset;
                        color: rgb(0,149,246);
                        font-size: 14px;
                        cursor: pointer;
                    }
                }
            }
            .download-box{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 10px 0;
                gap: 20px;
                &>.text{
                    font-size: 14px;
                    font-weight: 500;
                }
                &>.download-link-wrap{
                    display: flex;
                    gap: 8px;
                    &>.download-link{
                        width: 136px;
                        height: 40px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}